* {
  box-sizing: border-box;

}
html {
  width:100%;
  height:100%;
  line-height: 1;
  overflow-x: hidden;
}
body {

  width:100%;
  height:100%;
  font-family:$font;
  color:$font-color;
  overflow-x: hidden;

}
.si-page {

  width:100%;
  height:100%;
  // background-image: url(../images/bg-new.png);
  @include background($client);
  background-repeat:no-repeat;
  background-position: center center;
  background-size:cover;

}
.clearfix {
  display: block;
  content:"";
  clear:both;
}
.fontAwesome {
  font-family: "FontAwesome";
  content:"";
}
%vg-fonts {

  font-family:"vg-fonts";
  -webkit-font-smoothing:antialiased;
}
%ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
%d-flex {
  display: flex;
}
%align-center {
  align-items: center;
}
%justify-center {
  justify-content: center;
}
%justify-between {

  justify-content: space-between;
}
%justify-around {

  justify-content: space-around;

}
%align-start {
  align-items: flex-start;
}

.si-selectoption {
  position: relative;
  height:20px;
  border-bottom: 1px solid rgba(255,255,255,.20);
  select {
    position: absolute;
    width:100%;
    height:100%;
    opacity: 0;
    border:none;
    color:#4a4a4a;
    text-transform: uppercase;
    z-index: 2;
    @media (max-width: 768px) {
      opacity:1;
      display: block;
      background: transparent;
      color:#fff;
      appearance:none;

    }

  }
  @media (max-width:  768px) {
    .CaptionCont {
      display: none;
    }
  }
  .select-out {

    width:calc(100% - 10px);
    display: inline-block;
    color:rgba(255,255,255,0.5);
    text-transform: uppercase;
    font-size: 14px;
    overflow: hidden;
    // text-overflow: ellipsis;
    white-space: nowrap;
    @extend %ellipsis;

  }
  &:after {
    display: inline-block;
    position: absolute;
    right:-4px;
    bottom:0px;
    content:"";
    width:0px;
    height:0px;
    border-top:6px solid rgba(255,255,255,0.5);
    border-bottom:6px solid transparent;
    border-left:6px solid transparent;
    border-right:6px solid transparent;
    transform: rotate(-46deg);
    z-index: 1;
  }
  &.active {
    .select-out {
      color:#fff;
    }
    &:after {
      border-top:6px solid #fff;
    }
  }
}
.si-main {
  position: relative;
}
.nicescroll-cursors {

    right: -8px !important;
    background-color: rgba(255, 255, 255, 0.5) !important;
    border:none !important;
}

.disabled  , .templates .swiper-button-disabled {
  user-select: none;
  pointer-events: none;
  opacity: 0.5;
}

.grab {
  cursor: grab;
}
.grabbing {
  cursor: grabbing;
}
